<!-- by 添少 -->
<template>
  <section>
    <mt-header title="选择俱乐部">
      <router-link to="/" slot="left">
        <mt-button icon="back" @click="$router.go(-1)"></mt-button>
      </router-link>
      <mt-button @click="handNext" slot="right">下一步</mt-button>
    </mt-header>
    <div class="club">
      <div class="item hd">
        <span>选择俱乐部</span>
        <span>{{(userClubList[selectedIndex]||{name: '请选择'}).name}}</span>
      </div>
      <template v-for="(row,$index) in userClubList">
        <div class="item" :class="{active:$index===selectedIndex}" @click="handSelect($index)">
          <div>{{row.name}}</div>
          <div><b class="iconfont icon-success"></b></div>
        </div>
      </template>
    </div>
  </section>
</template>
<script>
  import {mapState} from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'

  export default {
    name: '',
    components: {
      Topbar,
      FixedLayout
    },
    props: {},
    data() {
      return {
        selectedIndex: -1,
        userClubList: [],
      }
    },
    computed: {
      ...mapState({})
    },
    watch: {},
    created() {
      this.init()
    },
    methods: {
      init() {
        this.$http.get(this.$port.GET_USER_CLUB_LIST, {
          params: {}
        }).then(res => {
          if (res.code === 200) {
            this.userClubList = res.data
          }
        })
      },
      initData() {
      },
      handSelect(index) {
        this.selectedIndex = index
      },
      handNext() {
        this.$store.commit(this.$actionType.set_article_data, {
          clubId: this.userClubList[this.selectedIndex].clubId
        })
        this.$router.push({
          path: 'edit',
          query: this.$route.query
        })
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .club {
    background-color: #fff;
    font-size: 16px;
    width: 100%;
    margin-top: 10px;
    .item {
      display: flex;
      border-bottom: solid #e5e5e5 1px;
      > * {
        padding: 12px 15px 13px;
        flex: 1;
      }
      > :last-child {
        text-align: right;
        color: #3aa7cf;
        visibility: hidden;
      }
      .iconfont {
        font-size: 16px;
      }
      &.active {
        background-color: #3aa7cf;
        color: #fff;
        > :last-child {
          color: #fff;
          visibility: visible;
        }
      }
    }
    .hd {
      color: #888;
      > :last-child {
        visibility: visible;
      }
    }
  }
</style>



